import Vue from 'vue';
import Vuex from 'vuex';
import {
  getCookie,
  setCookie,
  delCookie,
} from '../util/userCookie';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    collapsed: false,
    username: '',
    loginStatus: false,
    userInfo: getCookie(),
    menuList: [],
    filterList: [],
    headTitle: [{
      title: '',
      dataIndex: 'title',
    }, {
      title: '',
      dataIndex: 'desc',
    }, {
      title: '',
      dataIndex: 'categoryName',

    }, {
      title: '',
      dataIndex: 'tags',
    }, {
      title: '',
      dataIndex: 'price',
    }, {
      title: '',
      dataIndex: 'price_off',
    }, {
      title: '',
      dataIndex: 'upStatus',
    }, {
      title: '',
      dataIndex: 'inventory',
    }, {
      title: '',
      dataIndex: 'sale',
    },
    {
      title: '',
      dataIndex: 'action',
      key: 'action',
      scopedSlots: {
        customRender: 'action',
      },
    },
    ],
    category: [],
    breadcrumb: [],
  },
  mutations: {
    changeUser(state, user) {
      state.username = user;
    },
    userSetCookie(state, info) {
      state.userInfo = info;
    },
    userDelCookie(state, info) {
      state.userInfo = info;
    },
    changeList(state, list) {
      state.menuList = list;
    },
    changeFilterList(state, list) {
      state.filterList = list;
    },
    setBreadcrumb(state, data) {
      state.breadcrumb.push(data);
    },
    delBreadcrumb(state, index) {
      state.breadcrumb.splice(index, 1);
    },
    setCategory(state, data) {
      state.category = data;
    },
  },
  actions: {
    changeCollapsed() {
      this.state.collapsed = !this.state.collapsed;
    },
    setUserInfo({
      commit,
    }, info) {
      commit('userSetCookie', info);
      setCookie(info);
    },
    delUserInfo({
      commit,
    }) {
      // store
      commit('userDelCookie', delCookie());
      // cookie
    },
    changeMenuList({
      commit,
    }, menuList) {
      commit('changeList', menuList);
    },
    changeAsyncMenuList({
      commit,
    }, filterList) {
      commit('changeFilterList', filterList);
    },
    setBreadcrumbInfo({
      commit,
    }, data) {
      commit('setBreadcrumb', data);
    },
    delBreadcrumbInfo({
      commit,
    }, index) {
      commit('delBreadcrumb', index);
    },
    setCategoryInfo({
      commit,
    }, data) {
      commit('setCategory', data);
    },
  },
  modules: {},
});
